@layer components
	.indicator-swipe
		@apply z-30 px-3 
		@apply w-[calc(104/19.2*1rem)] h-14 rounded-full
		@apply flex justify-between items-center
		@apply transition-all pointer-events-none
		&:not(.active)
			@apply opacity-0
		svg
			@apply stroke-white
		.hand
			animation: moveSwipeGesture 2.25s infinite ease-in-out
			@apply md:w-16 md:h-12 w-10 h-10
	.nav-primary
		@apply flex items-center justify-center gap-4 
		@apply overflow-auto whitespace-nowrap
		@screen -md
			@apply justify-normal pb-3
		li
			a
				@apply inline-flex items-center justify-center 
				@apply rounded-5 px-4 h-10
				@apply bg-white text-Primary-5 font-semibold uppercase
			&.active, &:hover
				a
					@apply bg-Primary-5 text-white
	.pagination
		@apply flex items-center justify-center
		ul
			@apply flex items-center justify-center gap-6
			li
				a, span
					@apply inline-flex items-center justify-center
					@apply rem:w-[52px] rem:h-[52px] 
					@apply bg-Utility-300
					@apply font-normal
				&.active
					a, span
						@apply bg-Primary-1 text-white
				&:hover
					a
						@apply bg-Primary-1 text-white
	.product-item
		.img
			background: linear-gradient(180deg, #E8E8E8 0%, #DADADA 100%)
	.header-button-website
		button
			@apply inline-flex items-center gap-2 rem:h-[42px]
			@apply border border-white px-3 text-white
			.icon
				@apply sq-6 inline-flex items-center justify-center