.home-1
	// margin-top: calc(-1 * var(--header-height))
	@apply  h-auto
	.home-1-slide
		@apply h-full
		&::before
			content: ""
			background: linear-gradient(0deg, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0.35) 100%)
			@apply absolute w-full h-full top-0 left-0 pointer-events-none  z-1
		.play-icon
			background: rgba(0, 0, 0, 0.20)
			@apply absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-20
			@apply text-white inline-flex items-center justify-center
			@apply rem:w-[68px] rem:h-[68px]  rounded-full heading-1
			i
				@apply font-light
		.swiper
			@apply h-full
			.home-1-banner
				@apply h-full
				&::before
					content: ""
					background: linear-gradient(180deg, rgba(0, 0, 0, 0.20) 77.66%, rgba(0, 0, 0, 0.80) 100%)
					@apply absolute w-full h-full top-0 left-0 pointer-events-none  z-10
				a
					img
						@apply h-full w-full
					video
						@apply rem:h-[960px] w-full object-cover
				.home-1-content
					@apply absolute bottom-0 left-0 z-20 w-full text-white pb-16
					@apply flex items-center justify-between z-10
					.wrap-title
						@apply flex items-center gap-5 mb-2
						h2
							line-height: calc(96/80)
							@apply xl:rem:text-[80px] uppercase font-normal text-white
							&.title-2
								@apply text-white/80
						.line
							@apply h-12 w-[1px] bg-white/50
		.wrap-pagination
			backdrop-filter: blur(10px)
			@apply absolute bottom-10 right-20 z-20
			@apply bg-black/20 rounded-5 py-1 px-2
			.swiper-pagination
				@apply static text-right w-max flex items-center justify-center
				.swiper-pagination-bullet
					background-image: url('../img/bullet-normal.svg')
					background-size: contain
					background-repeat: no-repeat
					transition: all 0.3s ease
					border-radius: 0
					@apply bg-transparent opacity-100 rem:w-[14px] rem:h-[14px] mx-1
					&-active
						background-image: url('../img/bullet.svg')
						