.contact
	@apply relative z-10
	.contact-item
		@apply flex gap-4
	.map
		iframe
			@apply w-full rem:h-[336px]
	.col-left
		.contact-item
			.bottom
				a
					@apply hover:text-Primary-1
	.col-right
		@apply rounded-5
		.title
			span
				@apply text-Primary-1
		form
			.form-group
				input, textarea
					@apply border border-[#EFEFEF] rounded-3  font-normal h-12
					@apply inline-flex items-center justify-center w-full px-4
					&::placeholder
						@apply text-[#818181]
				&.textarea
					textarea
						@apply rem:min-h-[176px] h-full py-4 resize-none
			
