.btn
	@apply cursor-pointer select-none transition-all duration-300
	&.loading
		@apply bg-transparent #{!important}
		span,.icon
			@apply opacity-0
		&::before
			content: ''
			animation-duration: 0.5s
			@apply w-5 h-5 border-3 border-white/40 border-t-white rounded-full inline-block
			@apply absolute animate-spin
	&.btn-sw-1
		@apply sq-14 py-2 px-5 rounded-full  flex-center bg-Primary-1
		&::before
			@apply font-awesome text-Utility-white font-light
		&.btn-prev
			&::before
				content: '\f053'
		&.btn-next
			&::before
				content: '\f054'
				@apply font-awesome
		&:hover
			@apply bg-Primary-2
			&::before
				@apply text-Utility-white
	&.btn-add-cart
		@apply inline-flex items-center gap-3 text-Utility-500
		span
			@apply font-secondary
		.icon
			i
				@apply text-xl font-light
		&:hover
			@apply text-Primary-1
			.icon
				i
					@apply text-Primary-1
	&.btn-primary
		@apply inline-flex items-center justify-center
		@apply border border-black
		@apply rem:h-[44px]  px-4 bg-Utility-white  
		&:hover
			@apply text-white border-transparent bg-black
			span
				@apply text-Utility-white
		span
			@apply rem:text-[18px] font-normal text-black font-secondary
	&.btn-secondary
		backdrop-filter: blur(10px)
		@apply inline-flex items-center justify-center
		@apply border
		@apply border-white/25
		@apply rem:h-[60px]  px-6 bg-transparent text-white font-secondary
		&:hover
			@apply bg-white/80 border-white/25 text-Primary-1