.footer
	@apply text-Utility-Black
	.footer-top
		.footer-column
			ul.footer-menu
				@apply flex flex-col gap-4
				li
					a
						@apply rem:text-[16px] font-normal border-b-2 border-b-transparent
					&:hover
						a
							@apply border-b-Primary-1 text-Primary-1
			ul.footer-social-menu
				@apply flex items-center rem:gap-[10px]
				li
					a
						@apply sq-10 inline-flex items-center justify-center rounded-full bg-Utility-200
						i
							@apply text-Utility-Black font-normal
	.footer-bottom
		ul
			@apply flex items-center rem:gap-[23px]
			li
				a
					@apply body-4 font-light border-b border-b-transparent
				&:hover
					a
						@apply border-b-white text-white